#locations-list {
  padding: 0;
  list-style-type: none;
}

#locations-list li {
  cursor: default;
  width: 100px;
  height: 100px;
  margin: 10px;
  display: inline-block;
}

li svg {
  z-index: 0;
  width: 100%;
  height: 100%;
}

#locations-list li.clickable {
  cursor: pointer;
  fill-opacity: 1;
}

#locations-list li.clickable:hover {
  fill-opacity: .5;
}

#free-item-unchecked-svg {
  animation: 5s linear infinite color-shift;
}

@keyframes color-shift {
  0% {
    fill: red;
  }

  25% {
    fill: #80ff00;
  }

  50% {
    fill: #0ff;
  }

  75% {
    fill: #8000ff;
  }

  100% {
    fill: red;
  }
}

#free-item-checked-svg {
  fill: #4b4b4b;
}

li span {
  color: #fff;
  z-index: 1;
  width: 100px;
  cursor: inherit;
  padding-top: 20px;
  font-size: 16pt;
  font-weight: bold;
  position: absolute;
}
